import React from "react"

export default class IndexPage extends React.Component {
  state = { note_text: "# Example note\n" }
  updateText = event => {
    const target = event.target
    const value = target.value
    const name = target.name

    this.setState({
      note_text: value,
    })
  }
  render() {
    return (
      <div className="p-5">
        <div className="px-10 py-5">
          <button className="font-fira rounded p-2 bg-green-200 text-green-800 border border-green-900 hover:bg-green-500 outline-none">
            Save
          </button>
        </div>

        <p className="font-fira text-green-800 text-xl px-10 my-5">Source</p>
        <textarea
          autoFocus={true}
          className="w-full font-fira p-10 outline-none bg-green-100 border text-green-900 border-green-900 rounded"
          onChange={this.updateText}
          value={this.state.note_text}
        />
        <p className="font-fira text-green-800 text-xl px-10 my-5">Render</p>

        <div className="p-10 bg-green-100 rounded">
          {this.state.note_text
            .toString()
            .split("\n")
            .map(inputline => {
              let line = inputline
              /*
             `/^<latex> (.*$)/gim.test(line) ? (
                <img
                  src={`https://latex.codecogs.com/gif.latex?${encodeURIComponent(
                    line.replace("<latex> ", "")
                  )}`}
                />
              )
               */
              return /^# (.*$)/gim.test(line) ? (
                <p className="text-green-900 text-3xl font-bold font-slab">
                  {line.replace("# ", "")}
                </p>
              ) : /^## (.*$)/gim.test(line) ? (
                <p className="text-green-900 text-2xl font-bold font-slab">
                  {line.replace("## ", "")}
                </p>
              ) : /^### (.*$)/gim.test(line) ? (
                <p className="text-green-900 text-xl font-bold font-slab">
                  {line.replace("### ", "")}
                </p>
              ) : /^\s*$/gim.test(line) ? (
                <br />
              ) : /^<latex> (.*$)/gim.test(line) ? (
                <img
                  src={`https://latex.codecogs.com/gif.latex?${encodeURIComponent(
                    line.replace("<latex> ", "")
                  )}`}
                />
              ) : (
                <p className="text-green-900 font-lato">{line}</p>
              )
            })}
        </div>
      </div>
    )
  }
}
